	module.exports = {"data":{"markdownRemark":{"html":"<h1>Converting a.m./p.m. to 24-Hour Clock</h1>\n<p>There are two main ways to show the time: “12-Hour Clock” or “a.m./p.m.”: 24-Hour Clock.</p>\n<p>Most countries prefer the 24-Hour Clock, yet, in Latin America and English-speaking countries the 12-Hour Clock is broadly used, where twice a day it is 12:00, at midnight and at noon. To distinguish between both, you can add a.m. or p.m. respectively.</p>\n<h2>12-hour Clock (a.m./p.m.)</h2>\n<p>The day is split into 12 hours running from Midnight to Noon (a.m. hours) and 12 hours running from Noon to Midnight (p.m. hours).</p>\n<p>The abbreviations “a.m. and p.m.” derive from Latin:</p>\n<ul>\n<li>a.m.: Ante Meridiem = Before Noon.</li>\n<li>p.m.: Post Meridiem = Afternoon.</li>\n</ul>\n<h2>24-hour Clock</h2>\n<p>The day runs from midnight to midnight and is divided into 24 hours, indicated by the hours passed since midnight, from 0 to 23.</p>\n<h2>Converting a.m./p.m. to 24-hour Clock</h2>\n<h3>From the first hour of the day (12:00 am (Midnight) to 12:59 am), and subtract 12 hours.</h3>\n<ul>\n<li>12:00 a.m. = 0:00</li>\n<li>12:15 a.m. = 0:15</li>\n</ul>\n<h3>From 1:00 a.m. to 12:59 p.m., remains the same.</h3>\n<ul>\n<li>9:00 a.m. = 9:00</li>\n<li>12:59 p.m. = 12:59</li>\n</ul>\n<h3>From 1:00 p.m. to 11:59 p.m., add 12 hours.</h3>\n<ul>\n<li>3:17 p.m. = 15:17</li>\n<li>11:59 p.m. = 23:59.</li>\n</ul>\n<h2>Converting 24-hour Clok to a.m/p.m</h2>\n<h3>For the first hour of the day (0:00 to 0:59), add 12 hours and add the “a.m.” termination.</h3>\n<ul>\n<li>0:30 = 12:30 a.m.</li>\n<li>0:55 = 12:55 a.m.</li>\n</ul>\n<h3>From 1:00 to 11:59, just add the “a.m.” termination.</h3>\n<ul>\n<li>2:25 = 2:25 a.m.</li>\n<li>9:30 = 9:30 a.m.</li>\n</ul>\n<h3>From 13:00 to 23:59, subtract 12 hours, and add the “p.m.” termination.</h3>\n<ul>\n<li>16:55 = 4:55 p.m.</li>\n<li>21:45 = 8:45 p.m.</li>\n</ul>","fields":{"slug":"/mathematics/converting-am-pm-to-24-hour-clock/"},"frontmatter":{"title":"Converting a.m./p.m. to 24-Hour Clock"}}},"pathContext":{"slug":"/mathematics/converting-am-pm-to-24-hour-clock/"}}

